input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  background: transparent; /* Otherwise white in Chrome */
  width:40vh;
  margin-right:0;
  position:absolute;
  right:0;
  top:40vh;
  left:85vw;
  border-radius: 3px;

}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

input[type=range]::-ms-track {
  cursor: pointer;

  /* Hides the slider so custom styles can be added */
  background: transparent; 
  border-color: transparent;
  color: transparent;
}




input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  border-radius: 8px;
  background: transparent;
  cursor: pointer;
  margin-top: -4px;
  border: solid var(--firstColor) 4px;
}

/* All the same stuff for Firefox */
input[type=range]::-moz-range-thumb {
  border: 1px solid #000000;
  /* height: 36px; */
  width: 16px;
  border-radius: 3px;
  background: var(--firstColor);
  cursor: pointer;
  
}

/* All the same stuff for IE */
input[type=range]::-ms-thumb {
  /* border: 1px solid #000000; */
  /* height: 36px; */
  width: 16px;
  border-radius: 3px;
  background: var(--firstColor);
  cursor: pointer;
}




input[type=range]::-webkit-slider-runnable-track {
  width: 200%;
  height: 8.4px;
  cursor: pointer;
  background: -webkit-gradient(linear, left top, right top, from(rgba(12,20,69,1)), color-stop(50%, rgba(227,154,151,1)), color-stop(97%, rgba(249,215,28,1)));
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
  border-radius: 1.3px;
  /* border: 0.2px solid #010101; */
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: -webkit-gradient(linear, left top, right top, from(rgba(12,20,69,1)), color-stop(50%, rgba(227,154,151,1)), color-stop(97%, rgba(249,215,28,1)));
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
}

input[type=range]::-moz-range-track {
  width: 200%;
  height: 8.4px;
  cursor: pointer;
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
  border-radius: 1.3px;
  /* border: 0.2px solid #010101; */
}

input[type=range]::-ms-track {
  width: 200%;
  height: 8.4px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
  /* border: 0.2px solid #010101; */
  border-radius: 2.6px;
}
input[type=range]:focus::-ms-fill-lower {
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
}
input[type=range]::-ms-fill-upper {
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
  /* border: 0.2px solid #010101; */
  border-radius: 2.6px;
}
input[type=range]:focus::-ms-fill-upper {
  background: linear-gradient(90deg, rgba(12,20,69,1) 0%, rgba(227,154,151,1) 50%, rgba(249,215,28,1) 97%);
}