@-webkit-keyframes slideModal {
	from {top:-15vh}
		to {top:2vw}
}
@keyframes slideModal {
	from {top:-15vh}
		to {top:2vw}
}
@-webkit-keyframes closeSlideModal {
	from {top:2vh}
		to {top:-15vw}
}
@keyframes closeSlideModal {
	from {top:2vh}
		to {top:-15vw}
}
.SlideModal{
	cursor: pointer ;
	opacity: 0.85;
	z-index: 5;
	width:100%;
	height:100%;
	text-align:center;
	position:relative;
	overflow: hidden;
	display: inline-block;
	left: 50%;
	transform: translate(-55%,-50%);
	-webkit-transform: translate(-55%,-50%);
	-moz-transform: translate(-55%,-50%);
	-ms-transform: translate(-55%,-50%);
	-o-transform: translate(-55%,-50%);
	position:absolute;
	width: 100%;
	text-align: left;
	position: absolute;
	top: -15vh;
	width: 70vw;
	height: 12vh;
	background-color:var(--thirdColor);;
	font-size: 0.7em;
	margin: 3vw;
	border-radius: 5px;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	-webkit-transform: translatte(-55%,-50%);
}


.SlideModalChild{
	margin-top: 3vh;
	margin-left: 3vh;
	padding-right: 3vh;
	position:relative;
	width:90%;
	text-align: left;
}

.Close-Image{
		position: absolute;
		right: 2vh;
		top: 2vh;
}