.btnSlide {
    color: #fff;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    background-color: var(--secondColor);
    border-top: 1px solid var(--thirdColor);
    border-bottom: 1px solid var(--thirdColor);
    cursor:pointer;
    animation-fill-mode:forwards;
    font-size:1em;
    width:100%;
    height:8vh;
}

.btnSlide:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--secondColor);
    z-index: -2;
}
.btnSlide:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: var(--thirdColor);
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    z-index: -1;
}
.btnSlide:hover {
    color: #fff;
}
.btnSlide:hover:before {
    width: 100%;
}

.btnSlide spanTemp {
    position: absolute;
    top:0;
    width:100%;
    height: 100%;
    left:-100%;
    background-color: var(--secondColor);
    -webkit-animation: slide 0.5s;
    animation: slide 0.5s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
}

@-webkit-keyframes slide {
    from {left:-100%;}
    to {left: 0%;}
}

@keyframes slide {
    from {left:-100%;}
    to {left: 0%;}
}

.projectPanel{
    text-align:left;
    margin-top:7vh;
}

.projectPanelTitle{
    font-size:1.5em;
    margin-left:2vw;
}
.projectPanelList{
    margin-top: 7vh;
    padding: 0;
    border-top: 1px solid var(--thirdColor);
    border-bottom: 1px solid var(--thirdColor);
}

.panelListItem{
    display: inline-block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    position:relative;
    width:100%;
    padding-left:8vw;
}