:root {
	--firstColor: #eee9e5;
	--secondColor: #506868;
	--thirdColor: #2b393b;
	--fourthColor: #a1442c;
}

.App {
	text-align: center;
	background-color:var(--firstColor);
	color:var(--firstColor);
}

.App-header {
	background-color: #000000;
	min-height: 100vh;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-direction: row;
	flex-direction: row;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: var(--firstColor);
	position: sticky;
	top: -90vh;
	z-index: 5;
}

.Background-Image {
	height: 100vh;
	width: 100vw;
	background-attachment: fixed;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	font-family: Abel, sans-serif;
}

.Background-Base{
	position: absolute;
	top: 0;
	bottom:0;
	right: 0;
	left: 0;
	background-color:#000000;
	opacity: 0%;
}

@-webkit-keyframes fade-color {
	from {background-color:#000000; opacity: 0}
	to {background-color:var(--thirdColor); opacity: 1}
}

@keyframes fade-color {
	from {background-color:#000000; opacity: 0}
	to {background-color:var(--thirdColor); opacity: 1}
}
@-webkit-keyframes fade-out {
	from {background-color:var(--thirdColor); opacity: 1}
	to {background-color:#000000; opacity: 0}
}
@keyframes fade-out {
	from {background-color:var(--thirdColor); opacity: 1}
	to {background-color:#000000; opacity: 0}
}

.Header-Body{
	position: absolute;
	top: 0;
	bottom:0;
	right: 0;
	left: 0;
}

.UpperLeftIcons{
	float:left;
	text-align:left;
}

.Icon-Main{
	/* width: 100vw; */
	text-align: right;
	/* position: sticky; */
	top: 0;
	/* opacity: 50%; */
	background-color: #000;
}

.Icons{
	width: 7vw;
	text-align: center;
	float: right;
	margin-top: 5vh;
}

.Icon-Image{
	-webkit-transform-origin: center;
	-ms-transform-origin: center;
	transform-origin: center;
	cursor:pointer;
}

.Icon-Shuffle{
	display: inline-block;
	width: 7vw;
	text-align: center;
	margin-top: 5vh;
	/* opacity: 50%; */
	cursor:pointer;
	height:5vh;
}
.Icon-Heart{
	display: inline-block;
	width: 7vw;
	text-align: center;
	margin-top: 5vh;
	/* opacity: 50%; */
	cursor:pointer;
}
@-webkit-keyframes growHeart {
	from {
		/* opacity: 0; */
		/* width: 0px;
		height: 0px; */
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	to {
		opacity: 1;
		-webkit-transform: scale(1);
		transform: scale(1);
		/* width: 200px;
		height: 200px; */
	}
}

@keyframes growHeart {
	from {
		/* opacity: 0; */
		/* width: 0px;
		height: 0px; */
		-webkit-transform: scale(0);
		transform: scale(0);
	}

	to {
		opacity: 1;
		-webkit-transform: scale(1);
		transform: scale(1);
		/* width: 200px;
		height: 200px; */
	}
}

.Logo{
	margin-top: 30vh;
	font-size: 3em;
	font-weight: bold;
	overflow: hidden;
}

.Menu{
	display: inline-block;
	font-size: 1.2em;
	position: sticky;
	top: 5vh;
	text-align:center;
	padding-bottom: 1vh;
	margin-top: 20vh;
	color: var(--firstColor);
	/* margin-bottom: 10vh; */
}

.Menu-Logo{
	cursor:pointer;
	width: 0vw;
	float: left;
	text-align: center;
	overflow: hidden;
}

@-webkit-keyframes growBack {
	from {width:16.5vw;}
	to {width: 24vw;}
}

@keyframes growBack {
	from {width:16.5vw;}
	to {width: 24vw;}
}
@-webkit-keyframes shrinkBack {
	from {width:16.5vw;left: 0%}
	to {width: 0vw;left: -100%}
}
@keyframes shrinkBack {
	from {width:16.5vw;left: 0%}
	to {width: 0vw;left: -100%}
}

.Menu-Item{
	padding-top: 2vh;
	width: 24vw;
	float: left;
	text-align: center;
	color: var(--firstColor);
}

.Menu-Item-Text{
	text-align: center;
	position: relative;
	display: inline-block;
	cursor:pointer;
	color: var(--firstColor);
}

.Menu-Item-Text:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: 0;
	left: 0;
	background-color:var(--firstColor);
	visibility: hidden;
	-webkit-transform: scaleX(0);
	-ms-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.Menu-Item-Text:hover:before {
	visibility: visible;
	-webkit-transform: scaleX(1);
	-ms-transform: scaleX(1);
	transform: scaleX(1);
}

@-webkit-keyframes grow {
	from {width:0vw; left: -100%}
	to {width: 16.5vw; left: 0%}
}

@keyframes grow {
	from {width:0vw; left: -100%}
	to {width: 16.5vw; left: 0%}
}
@-webkit-keyframes shrink {
	from {width:24vw;}
	to {width: 16.5vw;}
}
@keyframes shrink {
	from {width:24vw;}
	to {width: 16.5vw;}
}

.Menu-Item-Icons{
	padding-top: 2vh;
	cursor:pointer;
	width: 0vw;
	float: left;
	text-align: center;
	overflow:hidden;
}

@-webkit-keyframes growRight {
	from {width:0vw; right: -100%}
	to {width: 7vw; right: 0%}
}

@keyframes growRight {
	from {width:0vw; right: -100%}
	to {width: 7vw; right: 0%}
}
@-webkit-keyframes shrinkBackRight {
	from {width:7vw;right: 0%}
	to {width: 0vw;right: -100%}
}
@keyframes shrinkBackRight {
	from {width:7vw;right: 0%}
	to {width: 0vw;right: -100%}
}

