.Menu-Dropdown{
	display: inline-block;
	font-size: 2em;
	/* color: #F3F3F3; */
	text-align:center;
	width:100%;
	height:100%
	/* margin-top: 30vh; */
}

.Menu-Item-Dropdown{
	text-align: center;
	cursor:pointer;
	position:relative;
	width:100%;
	height:17vh;
	background-color: transparent;
	/* border-top: 1px solid #647973; */
}

.Menu-Item-Dropdown-Text{
	text-align: center;
	display: inline-block;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	display:inline-block;
	position:absolute;
}

@-webkit-keyframes hoverIn {
	from {width:0%}
	to {width: 100%}
}

@keyframes hoverIn {
	from {width:0%}
	to {width: 100%}
}
@-webkit-keyframes hoverOut {
	from {width: 100%}
	to {width:0%}
}
@keyframes hoverOut {
	from {width: 100%}
	to {width:0%}
}

.Menu-Background{
	background-color: black;
	position : absolute;
	/* background-color : #647973; black//#314247; */
